import { addStyleSheetToHead } from "../utils/helper";

export const createPaginationStyle = ({
  bgColorActive = "#9AB87A",
  borderColor = "#3D315B",
  bgColor = "#444B6E",
  textColor = "#fff",
  textColorActive = "#000",
}) => {
  addStyleSheetToHead(
    `

      .wf-fslib-paginated-hide{
         display:none;
      }

      *[wf-fslib-paginated-hide="1"]{
        display:none;
      }

      *[wf-fslib-paginated-hide="2"]{
        display:unset;
      }

      .fs-pagination{
        display:inline-block;
        cursor:pointer;
      }


       .fs-pagination  a:hover {
            cursor: pointer;
        }

       .fs-pagination  ul {
            list-style: none;
            padding: 0;
            margin: 0;
            display: flex;
        }

       .fs-pagination  ul li {
            color: #fff;
            display: flex;
        }

       .fs-pagination  ul li a {
            background-color: ${bgColor};
            padding: 4px 8px;
            border: 1px solid ${borderColor};
            color: ${textColor};
            border-right: 0;
        }

       .fs-pagination  ul li.fs-pagination-active a {
             background-color: ${bgColorActive};
             color:${textColorActive}
        }
      .fs-pagination ul li:first-child a {
            border-radius: 5px 0 0 5px;
        }
        
       .fs-pagination ul li:last-child a {
            border-radius: 0 5px 5px 0;
            border-right: 1px solid ${borderColor};
        }`
  );
};
